<template>
  <div class="container">
    <section class="section">
      <h1 class="title">Форма для внесения пользователей в систему</h1>
      <h2 class="subtitle">
        Внизу дана форма с помощью которой можно добавить клиента в систему.
        Бизнесу важно знать E-mail пользователя и его ФИО
      </h2>
    </section>
    <div class="section">
      <div class="form">
        <div class="field">
          <label class="label">ФИО</label>
          <div class="control has-icons-left has-icons-right">
            <input class="input" type="text" placeholder="Text input" v-model="username">
            <span class="icon is-small is-left">
              <i class="fas fa-user"></i>
            </span>
            <span class="icon is-small is-right">
              <i class="fas fa-check"></i>
            </span>
          </div>
        </div>

        <div class="field">
          <label class="label">Email</label>
          <div class="control has-icons-left has-icons-right">
            <input class="input" type="email" placeholder="Email input" v-model="email">
            <span class="icon is-small is-left">
              <i class="fas fa-envelope"></i>
            </span>
            <span class="icon is-small is-right">
              <i class="fas fa-exclamation-triangle"></i>
            </span>
          </div>
        </div>

        <div class="field is-grouped">
          <div class="control">
            <button class="button is-link" @click="add">Подтвердить</button>
          </div>
          <div class="control">
            <button class="button is-link is-light" @click="clear">Очистить</button>
          </div>
        </div>
      </div>
    </div>
    <div class="section">
      <div class="table">
        <thead>
          <tr>
            <th><abbr title="ID">ID</abbr></th>
            <th><abbr title="Email">Email</abbr></th>
            <th><abbr title="Username">ФИО</abbr></th>
            <th><abbr title="Actions">Действия</abbr></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(user, index) in users" :key="index">
            <td>{{ index + 1 }}</td>
            <td>{{ user.username }}</td>
            <td>{{ user.email }}</td>
            <td><button class="button is-danger" @click="delete (user.username)">Удалить</button></td>
          </tr>
        </tbody>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'App',
  data() {
    return {
      username: null,
      email: null,
      users: [
        {
          username: 'Julia',
          email: 'julia@qa.com'
        },
        {
          username: 'Alex',
          email: 'prom@gmail.com'
        }
      ],
    }
  },
  methods: {
    delete(username) {
      this.users = this.users.filter(u => u.username !== username)
    },
    add() {
      this.users.push({ username: this.username, email: this.email })
    },
    clear() {
      this.username = null
      this.email = null
    }
  }
}
</script>

